 /* styles.css */


.about-container {
  max-width: 100%;
  margin-right: auto;
  margin-left: auto;
  padding-right: 1rem;
  padding-left: 1rem;
  margin-top: 160px;
  background-color: #6B46C1;
  color: #FFFFFF; 
  height: 600px;
}

.items-center {
  justify-content: space-evenly;
  display: flex;
  padding-top:100px;
 }

 

.text-yellow {
  color: #FBBF24;
}

.experience {
  font-size: 7rem;
}

.bold {
  font-weight: bold;
}

.margin-right {
  margin-right: 1rem;
}

.title {
  font-size: 4rem;
}

.name {
  font-size: 3rem;
}

.about-me {
  max-width: 32rem;
}

.about-me-container {
  display: flex;
}

.about-me-title {
  font-size: 2.25rem;
}
 

.about-me-description {
  text-align: start;
  font-size: 1.125rem;
  font-weight: 300;
  line-height: 32px;
  margin-bottom: 40px;
}

.btn {
  background-color: #FFFFFF;
  color: #6B46C1;
  padding: 0.75rem 1.5rem;
  width: 250px;
  border-radius: 0.5rem;
  font-weight: 600;
  margin-top: 1rem;
}

.btn:hover {
  color: white;
  background-color: black;
}
